// Forms
//
// ===========================================================================


@mixin search-form-field {
    display: flex;

    > label {
        @include media($desktop) {
            width: 33.33%;
        }

        width: 66.66%;
    }

    .search-field {
        width: 100%;
        margin: 0;
    }

    .search-submit {
        @include media($tablet) {
            width: 33.33%;
        }
    }
}
