// ===========================================================================
//
// Sidebar
//
// ===========================================================================

.widget-area {
    @include span-columns(4);
    @include tablet-single;
    @include media($tablet) {
        margin-top: unit(2);
        margin-bottom: unit(2);
    }

    top: rem(40);
    margin-top: rem(40);
    margin-bottom: rem(60);
}
.screen-reader-text {
    display: none;
}
.widget-title {
    @include type-style-h3;
    margin-top: 0;
    margin-bottom: 0;

    padding-top: ms(0);
    padding-bottom: ms(0);

    color: $aluminum;
}

body.single #secondary {
    top: 0;
    margin-top: rem(140);

    @include media($tablet) {
      margin-top: 0;
    }
}

// Toggle section on Taxonomy Region page (region main)
//
@include media( $tablet ) {
    body.tax-region .widget-area,
    body.home .widget-area {
        @include transition(right 0.5s ease-in-out);
        // display: none;
        position: absolute;
        top: 0;
        right: -100%;

        padding-right: $gutter / 2;
        padding-left: $gutter / 2;
        margin-top: $gutter / 2;
        margin-bottom: 0;

        &.active {
            // display: block;
            right: 0%;
        }
    }
}

// Widget
//
.widget {
    margin-bottom: ms(0);
    padding-top: ms(0);
    padding-bottom: ms(0);

    border-bottom: 1px solid $hex-border;
    &:first-child {
        padding-top: 0;
    }
    &:last-child {
        padding-bottom: 0;

        border-bottom: none;
    }
    ul {
        @include no-bullets;

        margin-left: 0;
        li {
            &:last-child {
            }
        }
    }
}

// Removes border & spacing for top widget position
//
.widget_search {
    @include zero-bottom;

    border-bottom: none;
    .search-submit {
        margin-bottom: 0;
    }
    .search-form {
        margin-bottom: ms(0);
    }
}

// Calendar widget
//
.widget_calendar {
    caption {
        margin-bottom: ms(-2);
    }
    table {
        width: 100%;

        text-align: center;
    }
    td,
    th {
        font-weight: inherit;
        line-height: ms(1);

        padding: ms(-1);

        border-bottom: 1px solid $hex-border;
    }
    tfoot td {
        border-bottom: none;
    }
    #prev {
        text-align: left;
    }
    #next {
        text-align: right;
    }
}

// Homepage Sidebar Vertical Sizing
//
$abs_article_spacing: 20px;
$abs_article_height: 275px;
$total_calculated_height: ($abs_article_spacing * 5) + ($abs_article_height * 4);

$pct_article_spacing: (($abs_article_spacing / $total_calculated_height) * 100) + 0vh;
$pct_article_height: (($abs_article_height / $total_calculated_height) * 100) + 0vh;

@media screen and (min-width: 769px) {
    body.tax-region .widget-area,
    body.home .widget-area,
    body.search-results .widget-area {

        top: $pct_article_spacing;
        position: sticky;

        .article-wrapper {
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
            margin-bottom: -($pct_article_spacing);
            height: 100vh;
        }

        article {
            max-height: 275px;
            margin-bottom: $pct_article_spacing;
            height: $pct_article_height;
        }
    }
}

@media screen and (min-width: 768px) and (max-height: 800px) {
    body.tax-region .widget-area,
    body.home .widget-area,
    body.search-results .widget-area {
        // placeholder for custom styles based on height.
    }
}
