// ===========================================================================
//
// Grid Settings
//
// ===========================================================================

// Global
$margin: $gutter;
$padding: $gutter;
$max-width: 1360px;
$gutter: golden-ratio(1.25em, 1);

// Breakpoints
$tablet: new-breakpoint(max-width 768px 4);
$mobile: new-breakpoint(max-width 480px 2);
$desktop: new-breakpoint(min-width 769px 12);
$short: new-breakpoint(max-height 920px min-width 769px 12);

@mixin media-tablet-desktop {
  @media only screen and (min-width: 770px) and (max-width: 960px) {
    @content
  }
}
