// ===========================================================================
//
// First Featured Articles
//
// ===========================================================================

article.first-featured {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    position: relative;

    width: 100%;
    height: 440px;
    margin-bottom: unit(2);
    padding-bottom: 0 !important;

    border-bottom: none;
    background-color: $hex-light-grey;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    .featured-entry-header {
        width: 310px;
        min-height: 300px;
        margin-top: unit(2);
        margin-left: unit(2);

        background-color: $hex-real-white;
    }
}

@include media($tablet) {
    article.first-featured:nth-child(n+2) {
        // duplicated from _second-featured-article. Needs refactoring
        float: left;

        overflow: hidden;

        width: calc(50% - #{unit(1)});
        height: 350px;
        margin-right: unit(2);

        background-color: $hex-light-grey;

        &:nth-child(odd) { // changed to odd
            margin-right: 0;
        }

        .featured-entry-meta {
            margin-top: rem(25);
            margin-left: rem(25);
        }

        .featured-entry-header {
            @include position(absolute, 50% null null null);
            margin: 0;

            height: (350px / 2);
            width: 100%;

            background-color: $hex-real-white;
        }

        // overrides
        .featured-entry-meta {
            @include position( absolute, -100% null null 0);
        }
        .featured-entry-categories {
            display: none;
        }
        .featured-entry-header {
            min-height: initial;
        }

        .first-featured__image {
            height: (350px / 2);
        }

        .first-featured__content {
            position: relative;
        }

    }
}

.first-featured__image {
    display: flex;

    width: 100%;
    height: 100%;

    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.first-featured__content {
    position: absolute;
    top: 0;
    left: 0;
}


@include media($mobile) {
    article.first-featured:nth-child(n) {
        // duplicated from _second-featured-article. Needs refactoring
        @include span-columns(2);

        position: relative;

        overflow: hidden;

        height: 350px;
        margin-right: 0;
        margin-bottom: $gutter / 2;

        background-color: $hex-light-grey;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;

        .featured-entry-meta {
            margin-top: rem(25);
            margin-left: rem(25);
        }

        .featured-entry-header {
            margin: 0;

            height: (350px / 2);
            width: 100%;

            background-color: $hex-real-white;
        }

        // overrides
        .featured-entry-meta {
            @include position( absolute, -100% null null 0);
        }
        .featured-entry-categories {
            display: none;
        }
        .featured-entry-header {
            min-height: initial;
        }

        .first-featured__image {
            height: (350px / 2);
        }

        .first-featured__content {
            position: relative;
        }

    }
}
