// ===========================================================================
//
// Secondary Header
//
// ===========================================================================

.secondary-header {
    height: $secondary-header-bar-height;

    border-bottom: 1px solid $hex-border;
    background: $hex-real-white;
}

%secondary-header-navigation-active {
    border-bottom-color: $hex-primary;
    color: $hex-primary;
}

.secondary-header-navigation {
    float: right;

    a {
        @include type-light-links;

        font-size: rem(13);
        line-height: $secondary-header-bar-height + $header-content-offset;

        display: block;

        height: $secondary-header-bar-height;
        padding: 0 rem(16);

        text-decoration: none;

        border-bottom: 2px solid transparent;
        &:hover {
            @extend %secondary-header-navigation-active;
        }
    }

    .current-menu-item a {
        @extend %secondary-header-navigation-active;
    }

    .menu-item-blog a {
        @extend %secondary-header-navigation-active;
    }

    > ul {
        margin: 0;
        padding: 0;

        list-style: none;

        @include media( $tablet ) {
            display: none;
        }

        li {
            float: left;

            &:last-child a {
                padding-right: 0;
            }
        }
    }
}
