.btn {
	font-size: rem(16);
	line-height: ms(2);

	display: inline-block;

	min-width: 150px;
	padding: 5px 30px;

	text-align: center;

	border-width: 1px;
	border-style: solid;
	border-radius: 4px;

	&:hover {
		text-decoration: none;
	}
}

.btn-sm {
	font-size: rem(13);
	line-height: rem(27);

	min-width: rem(130);
	padding: 0 rem(10);
}

.btn-round {
	border-radius: rem(13.5);
}

.btn-outline-white {
	color: $hex-real-white;
	border-color: $hex-real-white;

	&:hover {
		background-color: $hex-real-white;
	}
}

.btn-outline-blue {
	color: $hex-secondary;
	border-color: $hex-secondary;

	&:hover {
		color: $hex-real-white;
		background-color: $hex-secondary;
	}
}

.btn-outline-lightblue {
	color: $picton-blue;
	border-color: $picton-blue;

	.icon {
		color: $picton-blue;
	}

	&:hover {
		color: $hex-real-white;
		background-color: $picton-blue;
	}
}

.btn-green {
	color: $hex-real-white;
	border-color: $forest-green;
	background-color: $forest-green;

	&:hover {
		border-color: $forest-green;
		background-color: $forest-green;
	}
}
