// ===========================================================================
//
// Type Styles
//
// ===========================================================================

@mixin type-style-h1 {
    @include font-weight(light);
    @include media($tablet) {
        font-size: ms(3);
    }

    font-size: ms(4);
    line-height: ms(0);

    margin-top: ms(-2);
    margin-bottom: ms(-2);
    &:first-child {
        margin-top: 0;
    }
    a {
        @include link-hover-none;
    }
}

@mixin type-style-h2 {
    @include font-weight(light);

    font-size: ms(2);
    line-height: ms(1);

    margin-top: ms(0);
    margin-bottom: ms(-1);
}

@mixin type-style-h3 {
    @include font-weight(normal);

    font-size: ms(1);
    line-height: ms(0);

    margin-top: ms(0);
    margin-bottom: ms(0);
}

@mixin type-style-h4 {
    @include font-weight(normal);

    font-size: ms(0);
    line-height: ms(0);

    margin-top: ms(0);
    margin-bottom: ms(0);

    text-transform: uppercase;
}

@mixin type-style-h5 {
    @include font-weight(normal);

    font-size: ms(0);
    line-height: ms(0);

    margin-top: ms(0);
    margin-bottom: ms(0);
}

@mixin type-style-h6 {
    @include font-weight(normal);

    font-size: ms(0);
    line-height: ms(0);

    margin-top: ms(0);
    margin-bottom: ms(0);
}

@mixin type-style-small {
    font-size: rem(14);
    line-height: ms(1);
}

@mixin type-style-default {
    @include sophos-sans-regular;
    @include media($tablet) {
    }

    font-size: ms(0);
    line-height: ms(2);

    margin-top: ms(0);
    margin-bottom: ms(0);
}

@mixin code {
    font-family: $mono;
    font-size: 12px;
    line-height: 1.5em;
}

@mixin type-footer-links {
    @include transition-default;

    color: $hex-footer-text;

    &:hover {
        text-decoration: none;

        color: $hex-real-white;
    }
}

@mixin type-light-links {
    @include transition-default;

    color: $hex-light-grey;

    &:hover {
        text-decoration: none;

        color: $hex-primary;
    }
}

@mixin type-dark-links {
    @include transition-default;

    color: $hex-secondary;

    &:hover {
        text-decoration: none;

        color: $hex-primary;
    }
}

@mixin custom-list-item {
	&:before {
		display: inline-block;
		height: 1rem;
		width: 1rem;
		line-height: 1rem;
		content: ' + ';
		text-align: center;
		background-color:green;
		color: #fff;
		margin-right: .35rem;
	}
}
