.search-results {
    article:last-child {
        margin-bottom: unit(2);
        padding-bottom: 0;

        border: none;
    }

    .archive-title {
        font-family: SophosSansLight, $font-fallback;
        font-size: rem(24);
        line-height: rem(60);

        color: $oslo-gray;

        span {
            font-style: italic;

            color: $forest-green;
        }
    }
}

.not-found {
    .search-form {
        @include search-form-field;
    }
}
