// ===========================================================================
//
// Display Posts Listing
//
// Shortcode example:
// [display-posts category="cybersecurite" posts_per_page="15"
// wrapper="div" image_size="thumbnail" include_excerpt="true"
// include_date="true" order="DESC" orderby="date"]
//
// ===========================================================================

$display-posts-content-inset: 175px;

.display-posts-listing {
    margin-top: unit(3);
    .listing-item {
        position: relative;

        margin-bottom: unit(2);
    }

    .image {
        position: absolute;
        top: 0;
        left: 0;
    }

    .title {
        @include type-dark-links;

        font-size: rem(24);
        line-height: ms(1);

        display: block;

        padding-left: $display-posts-content-inset;
    }

    .date {
        font-size: rem(13);
        font-size: rem(13);
        line-height: ms(0);

        display: block;

        margin-top: rem(5);
        margin-bottom: unit(1);
        margin-left: 2px;
        padding-left: $display-posts-content-inset;

        color: $hex-light-grey;
    }

    .excerpt-dash {
        display: none;
    }

    .excerpt {
        font-size: rem(14);
        line-height: ms(2);

        display: block;

        padding-left: $display-posts-content-inset;
    }
}
