// ===========================================================================
//
// News Header
//
// ===========================================================================

.news-header {
    padding-top: rem(50);
    padding-bottom: rem(8);

    @include media($tablet) {
        height: $secondary-header-bar-height;

        padding-top: 0;
        margin-top: rem(-70);

        border-bottom: 1px solid $hex-white;
    }
}

.news-header-container {
    position: relative;
}

.news-branding {
    @include media($tablet) {
        left: $gutter / 2;
        bottom: 24px;
        top: 24px;
    }
    @include position(absolute, 0 null null $gutter);
    z-index: 1;
}

.news-title {
}

.news-logo {
    @include hide-text;
    @include svg-icon('sophos-news-logo', 221px, 34px);

    margin-top: 0;
    margin-bottom: 0;

    @include media($tablet) {
        @include svg-icon('sophos-news-logo', 145px, 22px);
        background-size: 145px 22px;
    }
}

// Desktop: Sophos News Nav
@include media( $desktop ) {
    .news-header-navigation {
        float: right;
        padding-left: 240px; // width of .news-branding
        margin-bottom: rem(-10);
        text-align: right;

        &.search-active {
            opacity: 0;
        }

        a {
            font-size: rem(13);
            line-height: rem(27);

            height: rem(29);
            padding: 0 rem(16);

            text-decoration: none;
        }

        > ul {
            margin: 0;
            padding: 0;

            list-style: none;

            li {
                display: inline-block;
                vertical-align: top;
            }

            a {
                // min-width: rem(130);
                padding: 0 rem(15);
                margin-bottom: rem(10);

                text-align: center;

                border-width: 1px;
                border-style: solid;
                border-radius: 4px;

                &:hover {
                    text-decoration: none;
                }

                color: $picton-blue;
                border-color: $picton-blue;

                .icon {
                    color: $picton-blue;
                }

                &:hover {
                    color: $hex-real-white;
                    background-color: $picton-blue;
                }

                font-size: rem(13);
                line-height: rem(27);

                display: block;

                height: rem(29);
                margin-left: rem(10);

                text-decoration: none;
            }

            li:last-child a {
                min-width: inherit;
            }

            .is-icon a {
                @include hide-text(inline-block);

                height: 29px !important;
                margin-left: rem(10);
                padding: 0 15px;

                vertical-align: middle;

                background-position: center !important;
            }

            .icon-search a {
                @include svg-icon('icon-search-secondary', 15px, 15px, $picton-blue);

                &:hover {
                    @include svg-icon('icon-search-secondary', 15px, 15px, $hex-real-white);
                }
            }
        }
    }
}


// Tablet & Mobile: Sophos News Nav
@include media( $tablet ) {
    .news-header-navigation {
        @include position( absolute, 70px 0 null 0 );
        z-index: 1;
        display: none;

        &.active {
            display: block;
        }

        > ul {
            @include no-bullets;

            background: $wild-sand;

            li {
                border-bottom: 1px solid $alto;

                &.active {
                    color: $hex-real-white;
                    background-color: $picton-blue;

                    a {
                        color: $hex-real-white;

                        &:after {
                            display: block;
                        }
                    }
                }
            }

            .icon-search {
              display: none;
            }

            a {
                display: block;
                position: relative;
                padding: rem(20);

                font-size: rem(20);
                line-height: rem(29);

                color: $sophos-blue;

                &:hover {
                    text-decoration: none;
                }

                &:after {
                    display: none;
                    content: '';

                    margin-top: -11px;

                    @include svg-icon('icon-menu-active-check', 22px, 22px);
                    @include position( absolute, 50% 28px null null );
                }
            }
        }
        .menu-item.icon-search {
            display: none;
        }
    }
}
@include media( $mobile ) {
    .news-header-navigation {
        top: 100px;
    }
}

.site-search-block {
    @include position( absolute, -21px $gutter null 310px );
    height: rem(70);

    border: 1px solid $hex-border;
    background: $hex-real-white;
    z-index: 10;

    input {
        margin: 0 0 0 rem(50);
        padding: rem(10) rem(8);
        height: rem(68);
        width: 90%;
        border: none;
        font-size: rem(36);
        font-family: 'SophosSansLight', $font-fallback;

        @include placeholder {
            color: $cornflower;
            font-family: 'SophosSansLight', $font-fallback;
        }
    }

    .search-submit {
        @include position( absolute, 25px null null 16px );
        @include svg-icon('icon-search-secondary', 18px, 18px, $picton-blue);
        width: 18px;
    }

    a[href="#search-close"] {
        @include position( absolute, 21px 22px null null );
        @include svg-icon('icon-search-close', 26px, 26px, $cornflower);
    }

    @include media( $tablet ) {
        top: 0;
        right: $gutter/2;
        left: $gutter/2;
        bottom: 0;
        border: none;
    }
}

.mobile-menu-toggle {
    @include position( absolute, 20px 55px null null );

    .tablet {
        display: block;
    }
    .mobile {
        display: none;
    }

    a {
        position: relative;
        display: block;
        font-family: 'SophosSansMedium', $font-fallback;
        font-size: rem(15);
        color: $bombay;
        text-transform: uppercase;
        text-align: center;
        text-decoration: none;

        &:after {
            @include position(absolute, 10px null null 58% );
            content: '';
            display: none;
            @include svg-icon('icon-arrow-toggle', 12px, 8px, $bombay);
            background-size: contain;
            @include transform( rotate(0deg) );
        }
    }

    &.active a:after {
        @include transform( rotate(180deg) );
    }

    @include media( $mobile ) {
        @include position( absolute, 70px 0 null 0 );
        background-color: #EBEBEB;
        border: 1px solid #E0E2E4;
        z-index: 1;

        .tablet {
            display: none;
        }
        .mobile {
            display: block;
        }

        &.active a:after {
            display: block;
        }

        body.archive & {
            a {
                font-size: rem(12);
                text-transform: none;
                text-align: left;
                padding-left: $gutter / 2;

                span {
                    color: $picton-blue;
                }

                &:after {
                    display: block;
                    left: auto;
                    right: $gutter / 2;
                }
            }
        }
    }
}

.mobile-search-toggle {
    @include position( absolute, 24px $gutter/2 null null );

    a {
        @include svg-icon('icon-search-secondary', 20px, 20px, $picton-blue);
    }
}

.mobile-menu-toggle,
.mobile-search-toggle {
    display: none;

    @include media( $tablet ) {
        display: block;
    }
}
