// ===========================================================================
//
// Link Board
//
// ===========================================================================

.link-board {
    @include media($mobile) {
        padding: 0;
    }

    margin: 0;

    border-top: 1px solid $duck-egg-blue;

    max-width: 250px;

    &-icon {
        position: absolute;
        top: rem(15);
        left: rem(15);

        display: block;

        margin: 0;
        padding: 0;

        background-size: contain;
    }


    &-item {
        position: relative;

        display: block;

        margin-bottom: 0;

        border: solid $duck-egg-blue;
        border-top-width: 0;
        border-right-width: 1px;
        border-bottom-width: 1px;
        border-left-width: 1px;

        &-name {
            @include transition(color);

            font-size: rem(13);
            line-height: ms(1);

            margin-bottom: rem(1);

            color: shade($steel, 20%);
        }

        &-meta {
            font-size: rem(11);
            font-style: italic;
            line-height: ms(1);

            min-width: rem(150);

            color: $steel;
        }

        &-area {
            @include link-hover-none;

            display: block;

            padding: rem(12) rem(15);

            &--with-icon {
                position: relative;

                padding-left: rem(40);
            }

            &:hover {
                .link-board-item-name {
                    color: $sophos-blue;
                }
            }
        }
    }
}

// WP primary menu
.drop-content {
    .sub-menu {
        @extend .link-board;
    }

    .menu-item {
        @extend .link-board-item;

        > a {
            @extend .link-board-item-name;
            @extend .link-board-item-area;

            &:hover {
                color: $sophos-blue;
            }
        }
    }

    .menu-item-description {
        @extend .link-board-item-meta;
    }
}

.drop-has-icons {
    .menu-item > a {
        position: relative;

        padding-left: rem(45);

        &:before {
            @extend .link-board-icon;

            content: '';
        }
    }
}


// Menu Icons
// ---------------------------------------------------------------------------

$drop-menu-icons: (
//  'url-segment-to-match'      : 'icon-file-name'
    'next-gen-firewall'         : 'products-xg-firewall',
    'unified-threat-management' : 'products-sg-utm',
    'secure-wifi'               : 'products-secure-wifi',
    'choose-swg'                : 'products-secure-web-gateway',
    'choose-email'              : 'products-secure-email-gateway',
    'puremessage'               : 'products-puremessage',
    'intercept-x'               : 'products-intercept-x',
    'endpoint-antivirus'        : 'products-endpoint-protection',
    'sophos-clean'              : 'products-sophos-clean',
    'mobile-control'            : 'products-mobile-control',
    'safeguard-encryption'      : 'products-safeguard-encryption',
    'server-security'           : 'products-server-protection',
    'sophos-home'               : 'products-sophos-home',
    'industries'                : 'solutions-industries',
    'initiatives'               : 'solutions-it-initiatives',
    'compliance'                : 'solutions-compliance',
    'oem-solutions'             : 'solutions-oem',
    'professional-services'     : 'solutions-professional-services',
    'threat-analyses'           : 'solutions-sophos-labs',
    'public-cloud'              : 'solutions-public-cloud'
);

@each $url, $icon in $drop-menu-icons {
    .drop-has-icons {
        a[href*=#{$url}]:before {
            @include svg-icon($icon, 18px, 18px, $sophos-blue);
        }
    }
}

