// ===========================================================================
//
// Archive Page
//
// ===========================================================================

.archive {

	.article-author-block {
		padding: 0;
		background-color: transparent;
	}

	.author-block-container {
		padding-bottom: rem(40);
		margin-bottom: rem(40);
	}

  .author-profile {
      @include media-tablet-desktop {
        max-width: rem(300);
      }
  }

	.author-description {
    @include media-tablet-desktop {
      margin-left: rem(330);
    }
		margin-left: rem(430);

		.author-bio {
			font-size: rem(24);
			line-height: rem(36);
			color: $aluminum;
		}

		@include media($tablet) {
			margin-left: 0;
		}
	}

	.author-name {
		clear: none;
		font-size: rem(48);
		line-height: rem(60);
		margin-bottom: rem(10);

		a {
			color: $tangaroa;
		}
	}

    .archive-title {
		font-family: SophosSansLight;
        font-size: rem(24);
        line-height: 1.25;
		padding: rem(15) 0;
        color: $oslo-gray;

		span {
			color: $forest-green;
			font-style: italic;
		}
    }

    article.second-featured {
        @include media( $desktop ) {
            @include span-columns(4);
            &:nth-child(3n+3) {
                @include omega;
            }
        }
        @include media( $tablet ) {
            @include span-columns(2);
            &:nth-child(2n+2) {
                @include omega;
            }
        }
        @include mobile-single;
    }

}
