// ===========================================================================
//
// Post Categories
//
// ===========================================================================

.single-post .entry-categories {
	@include no-bullets;
	@include clearfix;

	font-size: rem(12);
	color: $picton-blue;

	a {
		color: $picton-blue;
		padding: 0 rem(6);
		display: inline-block;
		white-space: nowrap;

		&:first-child {
			padding-left: 0;
		}
	}
}
