// ===========================================================================
//
// Header
//
// ===========================================================================

.header-container {
    height: $header-bar-height;
}

.site-header {
    // @include transition (padding, height $timing-default $easing-default);
    @include media($tablet) {
        @include position(fixed, 0px null null 0px);

        z-index: 2;

        width: 100%;
        height: $header-bar-height-mobile;
    }

    height: $header-bar-height;

    background-color: #005bb5;

    // Adjust for wp admin bar
    .admin-bar & {
        @include media($tablet) {
            top: $wp-admin-bar-height;
        }
    }
}

.site-branding {
    @include media($tablet) {
        left: $gutter / 2;
    }
    @include media( $mobile ) {
        left: 50%;
        margin-left: -(131px / 2);
    }
    @include position(absolute, (17px + $header-content-offset) null null $gutter);
}

.site-title {
}

.site-logo {
    @include hide-text;
    @include svg-icon('site-logo', 131px, 21px, $hex-real-white);

    margin-top: 0;
    margin-bottom: 0;

    @include media( $mobile ) {
        &:after {
            display: none; // Temporary
            @include position(absolute, 6px -24px null null);
            content: '';
            // display: inline-block;
            @include svg-icon('icon-arrow-toggle', 12px, 8px, $hex-real-white);
            background-size: contain;
            opacity: 0.6;
        }
    }
}
.site-description {
    display: none;
}

// Admin bar
//
#wpadminbar {
    @include media($tablet) {
        @include position(fixed, 0px null null 0px);
    }
}
