// ===========================================================================
//
// Fourth Featured Articles
//
// ===========================================================================

article.fourth-featured.post {
    @include media( $tablet ) {
        margin-bottom: $gutter / 2;
    }
    display: flex;
    overflow: hidden;

    width: 100%;
    margin-bottom: unit(2);
    padding: 0;

    max-height: 373px;

    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    &:before {
        content: '';
        padding-bottom: percentage(373/708);
    }

    .featured-entry-header {
        padding: 50px 50px 45px;
        width: 100%;

        display: flex;
        flex-direction: column;
        align-self: stretch;
        align-items: flex-start;
        justify-content: center;

        .btn {
          @include media($short) {
            display: none;
          }
        }
    }

    .featured-entry-title {
        font-size: rem(28);
        line-height: 1.125;

        margin: 0;
        margin-bottom: rem(20);

        @include media($short) {
          margin-bottom: 0;
        }
    }

    .featured-entry-header a {
        @include transition-default;

        color: $hex-real-white;
    }

    &:nth-child(1) .featured-entry-header {
        background-color: rgba(0,173,201,.9);

        a.btn:hover {
            color: rgba(0,173,201,.5);
        }
    }

    &:nth-child(2) .featured-entry-header {
        background-color: rgba(120,60,189,.5);
        a.btn:hover {
            color: rgba(120,60,189,.5);
        }
    }

    &:nth-child(3) .featured-entry-header {
        background-color: rgba(63,174,41,.5);
        a.btn:hover {
            color: rgba(63,174,41,.5);
        }
    }

    &:nth-child(4) .featured-entry-header {
        background-color: rgba(249,110,17,.5);
        a.btn:hover {
            color: rgba(249,110,17,.5);
        }
    }
}

@include media( $tablet ) {
    article.fourth-featured.post {

    }
}
