// ===========================================================================
//
// Footer
//
// ===========================================================================


// Site Footer (Primary)
// ---------------------------------------------------------------------------

.site-footer {
    @include media($tablet) {
        padding-top: unit(2);
        padding-bottom: 0;
    }

    font-size: rem(14);

    padding-top: unit(4);
    padding-bottom: unit(4);

    color: $hex-footer-text;
    border-top: 1px solid $hex-border;
    background-color: $hex-primary-footer-bg;

    > .container {
        position: relative;
        @include media($desktop) {
            padding-left: calc(#{$gutter} + 75px);
        }
        @include media($tablet) {
            padding-left: 0;
            padding-right: 0;
        }
    }

    .site-info {
        @include type-style-small;
    }

    a {
        @include type-footer-links;
    }
}

.site-footer-title {
    display: inline-block;

    margin-bottom: rem(10);

    text-transform: uppercase;

    color: $hex-footer-title;
}

.site-footer-links {
    @include tablet-single;
    @include span-columns(2.4);

    line-height: ms(2);

    @include media($tablet) {
        &:last-child .site-footer-title {
            margin-bottom: 0;
        }
    }

    ul {
        @include no-bullets;
        @include sophos-sans-light;

        font-size: rem(13);

        li {
        }
    }
}

@include media( $tablet ) {
    .site-footer-links {
        margin: 0;

        text-align: center;

        border-top: 1px solid $hex-footer-border;

        &:last-child {
            border-bottom: 1px solid $hex-footer-border;
        }

        .menu-item {
            padding: .25rem 1.5rem;

            text-align: left;

            a {
                display: block;
            }
        }

        .menu:not(.menu-visible-on-mobile) {
            display: none;
        }
    }

    .site-footer-title {
        position: relative;

        display: block;

        margin-bottom: 0;
        padding: .5rem 1rem;

        cursor: pointer;
        text-align: left;

        &:hover {
            color: $hex-real-white;
        }

        &.submenu-visible-on-mobile {
            color: $hex-real-white;
            &::after {
                @include transform( rotate(180deg) );
                @include svg-icon('icon-arrow-toggle', 12px, 8px, $hex-real-white);
            }
        }

        &::after {
            @include transition-default(transform);
            @include svg-icon('icon-arrow-toggle', 12px, 8px, $hex-footer-text);
            @include transform( rotate(0deg) );

            position: absolute;
            top: calc(50% - 4px);
            right: 1rem;

            content: '';

            background-size: contain;
        }
    }
}

.social-footer-links {
    @include position( absolute, 0 null null $gutter );

    .social-sharing {
        li {
            float: none;
        }

        a {
            margin-bottom: 1rem;

            height: rem(38);
    		width: rem(38);
            line-height: rem(38);

            background-size: 19px 19px;
        }
    }
}

@include media( $tablet ) {
    .social-footer-links {
        position: static;

        .social-sharing {
            display: flex;
            flex-direction: row;
            justify-content: center;

            li {
                margin: 0 rem(5);
            }
        }
    }
}


// Secondary Footer
// ---------------------------------------------------------------------------

.secondary-footer {
    @include sophos-sans-light;

    font-size: rem(12);

    padding-top: unit(1);
    padding-bottom: unit(1);

    color: $hex-footer-text;
    background-color: $hex-secondary-footer-bg;

    .site-logo {
        @include media-tablet-desktop {
            display: none;
        }
        @include media( $tablet ) {
            display: none;
        }

        display: inline-block;

        width: 88px;
        height: 14px;
        margin-right: unit(1);

        vertical-align: -1px;

        background-size: contain;
    }

    a {
        @include type-footer-links;
    }
}

.site-copyright {
    @include media( $tablet ) {
        display: block;
        float: none;

        margin-right: 0;

        text-align: center;
    }

    display: inline-block;

    margin-right: unit(1);
}

.site-legal-navigation {
    @include media( $tablet ) {
        display: none;
        float: none;

        text-align: center;
    }

    float: right;

    > ul {
        @include no-bullets;

        margin: 0;

        li {
            @include media-tablet-desktop {
                margin-left: unit(1);
            }
            display: inline-block;

            margin-left: unit(2);

            &:first-child {
              margin-left: 0;
            }
        }

        .menu {
            @include media( $tablet ) {
                display: block;

                margin: 0;
            }

            display: inline;
            margin-left: 0;

            .menu-item {
                @include media( $tablet ) {
                    display: block;

                    margin-left: 0;
                }
            }
        }
    }
}


// Mobile Footer (article selection)
// ---------------------------------------------------------------------------

.mobile-footer {
    display: none;
    @include position(fixed, null 0 0 0);

    background: $hex-real-white;
    border-top: 1px solid $iron;

    max-height: $mobile-footer-height;

    @include media( $tablet ) {
        display: block;
    }

    ul {
        @include clearfix;
        @include no-bullets;
        margin: 0;

        li {
            float: left;
            width: 50%;
        }
    }

    a {
        display: block;
        padding: rem(10) 0 rem(8);

        border-bottom-width: 2px;
        border-bottom-style: solid;
        border-bottom-color: transparent;

        font-family: "SophosSansSemibold";
        font-size: rem(13);
        color: $hex-light-grey;

        text-transform: uppercase;
        text-decoration: none;
        text-align: center;

        &:hover {
            color: $sophos-blue;
            border-bottom-color: $sophos-blue;
        }
    }

    li.active a {
        color: $sophos-blue;
        border-bottom-color: $sophos-blue;
    }
}
