// ===========================================================================
//
// Drop Tool Tip / Menu
//
// ===========================================================================


.drop-menu-item {
    display: inline;
    .drop-content {
        display: none;
    }
}

// WP primary menu
.primary-header-navigation {
    .menu-item-has-children {
        display: inline;

        .drop-enabled {
            opacity: 1;
        }

        .sub-menu {
            display: none;
        }
    }
}

.drop-menu-item--text {
    .drop-target {
        position: relative;

        margin-right: rem(15);
        padding-right: rem(20);

        &:after {
            @include position(absolute, 47% 0px null null);
            @include svg-icon('icon-arrow-toggle', 12px, 8px, $hex-real-white);

            display: inline-block;

            content: '';

            opacity: .8;
            background-size: contain;
        }
    }

    .drop-enabled {
        &:after {
            transform: scaleY(-1);
        }
    }
}

// WP primary menu
.primary-header-navigation {
    @extend .drop-menu-item--text;
}

.drop-menu-item--footer {
    display: inline !important;
    .drop-target {
        margin-right: 0;
        &:after {
            top: 25%;
        }
    }
}


.drop-element {
    z-index: 10;
    // width: rem(300);
    // padding: rem(15);
    // border: 1px solid $alto;
    // border-radius: rem(2);

    background-color: white;
    // box-shadow: 0 1px 3px 0 rgba(0, 0, 0, .1);
    p {
        font-size: rem(14);
        line-height: ms(1);

        margin: 0;
    }
}

.popover .drop-content {
    display: none;
}

.drop-content {
    opacity: 0;
    background-color: $hex-real-white;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, .1);
    &:before {
        @include position(absolute, -12px null null 50%);

        display: block;

        width: 0;
        height: 0;
        margin-left: rem(-16);

        content: '';

        border-right: rem(16) solid transparent;
        border-bottom: rem(10) solid $hex-real-white;
        border-left: rem(16) solid transparent;
    }
    .drop-open .drop-element & {
        z-index: 11;

        margin-top: -6px;

        opacity: 1;
    }
}

.drop-element-attached-bottom {
    position: relative;

    .drop-content {
        &:before {
            display: none;
        }
    }
}
