.sophos-inline-ad *,
.sophos-inline-ad *:before,
.sophos-inline-ad *:after {
	box-sizing: inherit;
}

.s-button {
	-webkit-transition: all 0.15s linear;
	transition: all 0.15s linear;
	font-size: 0.875rem;
	line-height: 1.5;
	color: #f7f7f7;
	font-family: SophosSansMedium, Helvetica Neue, Helvetica, Arial, sans-serif;
	font-weight: 400;
	font-style: normal;
	display: inline-block;
	padding: 0.3125rem 1.25rem;
	cursor: pointer;
	text-align: center;
	text-decoration: none;
	border: 1px solid #007ac6;
	border-radius: 3px;
	background-color: #007ac6;
	text-shadow: none;
}

.s-button:hover {
	text-decoration: none;
	color: #fff;
	border-color: #003b60;
	background-color: #003b60;
}

.s-button--small {
	font-size: 0.75rem;
}

.s-ad-intercept-x {
	font-size: 1rem;
	line-height: 1.5;
	color: #242629;
	font-family: SophosSansRegular, Helvetica Neue, Helvetica, Arial, sans-serif;
	font-weight: 400;
	font-style: normal;
	line-height: 1.25;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	max-width: 769px;
	margin: 15px auto;
	padding: 26px 30px 30px;
	-webkit-transition: box-shadow 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
	transition: box-shadow 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
	background-color: #031b2b;
	background-image: url(data:image/jpeg;base64,);
	background-repeat: no-repeat;
	background-position: right -150px center;
	background-size: cover;
	box-shadow: 0 0 0 0 0 transparent;
	-webkit-box-pack: end;
	-ms-flex-pack: end;
	justify-content: flex-end;
	-webkit-box-align: baseline;
	-ms-flex-align: baseline;
	align-items: baseline;
}

a.s-ad-intercept-x__link-wrapper {
	text-decoration: none;
}

.s-ad-intercept-x__link-wrapper:hover .s-ad-intercept-x {
	box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.15);
}

.s-ad-intercept-x__sophos-logo {
	text-align: right;
}

