.s-ad-intercept-x__content {
	width: 100%;
	text-align: right;
}

.s-ad-intercept-x__title {
	font-size: 22px;
	line-height: 1.125;
	margin-top: 20px;
	margin-bottom: 20px;
	color: #fff;
}

.s-button--orange {
	color: #000;
	border-color: #f38332;
	background-color: #f38332;
}

.s-button--orange:hover {
	color: #fff;
	border-color: #e5680d;
	background-color: #e5680d;
}

@media (max-width: 768px) {
	.s-ad-intercept-x {
		background-position: right -320px center;
	}
	.s-ad-intercept-x__sophos-logo-svg {
		margin-top: 15px;
	}
}

@media (max-width: 480px) {
	.s-ad-intercept-x {
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-ms-flex-direction: column;
		flex-direction: column;
		min-width: 240px;
		text-align: center;
		background-image: -webkit-radial-gradient( 50% 123%,
		#1e549c 0,
		#00040f 100%);
		background-image: radial-gradient(50% 123%, #1e549c 0, #00040f 100%);
		background-position: center 0;
		background-size: 424px auto;
	}
	.s-ad-intercept-x__sophos-logo {
		text-align: center;
	}
	.s-ad-intercept-x__sophos-logo-svg {
		width: 64px;
		height: 11px;
		margin-top: 0;
		background-size: 64px 11px;
	}
	.s-ad-intercept-x__x-logo-img {
		display: inline-block;
		width: 220px;
		height: 98px;
		margin-top: 15px;
		margin-left: 7px;
		vertical-align: bottom;
		background-image: url(data:image/png;base64,);
		background-repeat: no-repeat;
		background-size: 220px 98px;
	}
	.s-ad-intercept-x__content {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-ms-flex-direction: column;
		flex-direction: column;
		width: 100%;
		margin-left: 0;
		text-align: center;
		-webkit-box-align: center;
		-ms-flex-align: center;
		align-items: center;
	}
	.s-ad-intercept-x__title {
		font-size: 14px;
	}
	.s-ad-intercept-x__dead {
		display: block;
	}
}
