// ===========================================================================
//
// Fonts
//
// Notes:
// - Uncomment weights below as needed and use provided mixins to
//   ensure consistency.
//
// Active weights / mixins:
// - @include sophos-sans-regular;
// - @include sophos-sans-semibold;
// - @include sophos-sans-medium;
//
// ===========================================================================

$helvetica: 'Helvetica Neue', Helvetica, Arial, sans-serif;
$sophos-sans: 'SophosSansRegular', $helvetica;
$mono: Monaco, Consolas, 'Lucida Console', monospace;
$font-fallback: $helvetica;
$font-path: 'fonts/';

// ===========================================================================
// Utilities
// ===========================================================================

@mixin make-font($family, $style: normal, $weight: normal) {
    font-family: $family, $font-fallback;
    font-weight: $weight;
    font-style: $style;
}

@mixin add-font-face($family, $file-name, $style: normal, $weight: normal) {
    @font-face {
        font-family: $family;
        font-weight: $weight;
        font-style: $style;

        src: url('#{$font-path}/#{$file-name}.eot'); /* IE9 Compat Modes */
        src: url('#{$font-path}/#{$file-name}.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
             url('#{$font-path}/#{$file-name}.woff2') format('woff2'), /* Modern Browsers */
             url('#{$font-path}/#{$file-name}.woff') format('woff'), /* Modern Browsers */
             url('#{$font-path}/#{$file-name}.ttf') format('truetype'), /* Safari, Android, iOS */
             url('#{$font-path}/#{$file-name}.svg#font') format('svg'); /* Legacy iOS */
    }
}


// ===========================================================================
// Sophos Sans
// ===========================================================================

// SophosSansLight (SophosSans-Light)
// ---------------------------------------------------------------------------

@include add-font-face(SophosSansLight, SophosSans-Light);

@mixin sophos-sans-light {
    @include make-font(SophosSansLight);
}


// SophosSansLightItalic (SophosSans-LightItalic)
// ---------------------------------------------------------------------------


// SophosSansRegular (SophosSans-Regular)
// ---------------------------------------------------------------------------

@include add-font-face(SophosSansRegular, SophosSans-Regular);

@mixin sophos-sans-regular {
    @include make-font(SophosSansRegular);
}


// SophosSansRegularItalic (SophosSans-RegularItalic)
// ---------------------------------------------------------------------------


// SophosSansMedium (SophosSans-Medium)
// ---------------------------------------------------------------------------

@include add-font-face(SophosSansMedium, SophosSans-Medium);

@mixin sophos-sans-medium {
    @include make-font(SophosSansMedium);
}

// SophosSansMediumItalic (SophosSans-MediumItalic)
// ---------------------------------------------------------------------------


// SophosSansSemibold (SophosSans-Semibold)
// ---------------------------------------------------------------------------

@include add-font-face(SophosSansSemibold, SophosSans-Semibold);

@mixin sophos-sans-semibold {
    @include make-font(SophosSansSemibold);
}

