// ===========================================================================
//
// Single Post/Page with Sidebar
//
// ===========================================================================

.single-post #primary article.post,
.page #primary article.page {
    margin-bottom: 0;
    padding-bottom: 0;

    .entry-header {
        @include clearfix;
        margin-bottom: rem(40);

		.entry-meta {
			float: left;
	        text-transform: uppercase;
	        color: $oslo-gray;

	    	a {
	    		color: $picton-blue;
	    	}

	        .posted-on a {
	            color: $oslo-gray;
	        }
		}

		.entry-categories {
			margin-bottom: rem(13);
		}
    }

    .entry-title {
        @include sophos-sans-medium;
        font-size: rem(46);
        line-height: rem(54);
        color: $tangaroa;
        margin-bottom: rem(8);
    }

    .entry-excerpt {
        padding: rem(20) 0;

        font-size: rem(18);
        line-height: rem(26);
        color: $oslo-gray;

        p {
            margin-bottom: 0;
        }
    }

    .entry-featured-image {
        margin-bottom: rem(40);

        img {
            display: block;
            margin: 0;
            width: 100%;
        }
    }

    .entry-meta {
		line-height: auto;
		font-size: rem(18);
		color: $tangaroa;

		.vcard {
			&:not(:last-child):after {
				content: ', ';
			}
		}

		@include media( $tablet ) {
			font-size: rem(14);
			line-height: rem(28);
		}
    }

    .social-sharing {
        float: right;

        li:first-child a {
    		text-indent: 0;
    	}

        @include media($tablet) {
            float: none;
            clear: both;

            padding-top: rem(3);
        }
    }

    .entry-content {
        font-size: rem(18);
        color: $tangaroa;

		// Some French and Italian posts have h1 and images at the
		// top of the content to mimic the featured image and title
		h1:first-child {
			display: none;

			& + p img:first-child {
				display: none;
			}
		}

		@include media( $tablet ) {
			font-size: rem(14);
            line-height: rem(28);
		}
    }

    @include media( $tablet ) {
        .entry-title {
            font-size: rem(22);
            line-height: rem(31);
        }

        .entry-excerpt {
            font-size: rem(15);
            line-height: rem(24);
        }

        .entry-meta {
            > span {
                display: block;
                margin-bottom: rem(12);
            }

            .byline {
                margin-left: 0;
            }
        }
    }
}

/**
 * Set a comfortable max-width for full-width content
 */
.page-template-full-width,
.post-template-full-width-title-excerpt-image,
.post-template-full-width-title-excerpt-overlay-image,
.page-template-full-width-title-excerpt-image,
.page-template-full-width-title-excerpt-overlay-image {
    .entry-content {
        max-width: $full-width-content-width;
        margin-right: auto;
        margin-left: auto;
    }
}
