// ===========================================================================
//
// Full Width - Excerpt above Image
//
// ===========================================================================

.post-template-full-width-title-excerpt-image #primary article.post,
.page-template-full-width-title-excerpt-image #primary article.page {

    .entry-header {
        padding: rem(60) rem(210);
        margin-bottom: 0;
    }

    .entry-title {
        font-size: rem(48);
        line-height: rem(60);
        color: $shark;
    }

    .entry-excerpt {
        font-size: rem(24);
        line-height: rem(36);
        color: $shark;
        padding: rem(8) 0 rem(25);
    }

    .entry-meta {
        margin-top: 0;

        > span {
            display: block;
        }

        .byline {
            font-size: rem(12);
            line-height: rem(36);
            margin: 0;
        }
    }

    .social-sharing {
        float: none;
        display: flex;
        justify-content: center;

        margin: rem(50) 0;

        li:last-child {
            margin-right: 0;
        }
    }

}

.post-template-full-width-title-excerpt-image .related-articles,
.page-template-full-width-title-excerpt-image .related-articles {

    .related-articles-title {
        font-size: rem(18);
        line-height: rem(30);
        color: $tundora;
        text-transform: uppercase;
        padding-top: rem(30);
        margin-bottom: rem(20);
    }

    article.second-featured {
        @include span-columns(4);
        @include tablet-single;

        margin-bottom: rem(40) !important;
    }
}

@include media($tablet) {
    .post-template-full-width-title-excerpt-image #primary article.post,
    .page-template-full-width-title-excerpt-image #primary article.page {

        .entry-header {
            padding: 0;
        }

        .entry-featured-image {
            margin-bottom: rem(18);
        }

        .social-sharing {
            margin: 0;
            padding: 0;
        }
    }
}
