// ===========================================================================
//
// Article Social Links
//
// ===========================================================================

.social-sharing {

	margin-bottom: 0;

	li {
		@include hide-text;
		position: relative;
	}

	a {
		display: block;
		height: rem(28);
		width: rem(28);
		border-radius: 100%;

		font-family: SophosSansLight;
		line-height: rem(28);
		text-align: center;

		background-repeat: no-repeat;
		background-position: center;
		background-color: $sophos-blue;
		color: $hex-real-white;

		&:hover {
			text-decoration: none;
		}
	}

	.comments:after {
		@include position(absolute, null null -1px -1px);
		@include transform(rotate(-38deg));
		content: '';
		display: block;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 4px 8px 4px 0;
		border-color: transparent $sophos-blue transparent transparent;
	}

	.twitter a,
	a[href*="twitter"] {
		background-image: svg-load('../svg/icon-twitter.svg', fill=#{$hex-real-white});
	}
	.facebook a,
	a[href*="facebook"] {
		background-image: svg-load('../svg/icon-facebook.svg', fill=#{$hex-real-white});
	}
	.googleplus a,
	a[href*="google"] {
		background-image: svg-load('../svg/icon-googleplus.svg', fill=#{$hex-real-white});
	}
	.linkedin a,
	a[href*="linkedin"] {
		background-image: svg-load('../svg/icon-linkedin.svg', fill=#{$hex-real-white});
	}
	.rss a {
		background-image: svg-load('../svg/icon-rss.svg', fill=#{$hex-real-white});
	}
	.youtube a,
	a[href*="youtube"] {
		background-image: svg-load('../svg/icon-youtube.svg', fill=#{$hex-real-white});
	}
}
