// ===========================================================================
//
// Author Article Block
//
// ===========================================================================

.article-author-block {
    padding: rem(30) rem(40) 0 rem(20);
    background-color: $hex-real-white;

	@include media($tablet) {
		padding: rem(30) rem(20) 0 rem(20);
	}
}

body.single-post .article-author-block {
    margin-bottom: rem(50);
	overflow: hidden;
}

.author-block-container {
	@include clearfix;

	margin-bottom: rem(30);
}

.author-title {
	@include type-style-h2;

	margin-top: 0;
	margin-bottom: rem(30);

    font-size: rem(24);
    color: $tangaroa;
}

.author-profile {
	float: left;

	@include media($tablet) {
		float: none;
		margin-bottom: rem(20);
	}
}

.author-description {
	margin-left: rem(175);

	@include media($tablet) {
		margin-left: 0;
	}

	.author-name,
	.author-more-link {
		font-family: SophosSansMedium;
	}

	.author-name {
		clear: none;
		font-size: rem(24);
		line-height: rem(30);
		margin-bottom: rem(10);
	}

	.author-bio,
	.author-more-link a {
		font-size: rem(14);
		line-height: rem(21);
		color: $aluminum;
	}

	.author-more-link {
		text-transform: uppercase;
	}
}
