// ===========================================================================
//
// Main Content
//
// ===========================================================================

.content-area,
body.tax-region .content-area {
    @include span-columns(8);
    @include tablet-single;
    @include media($desktop) {
        padding-right: unit(1);
    }
    @include media($tablet) {
        margin-top: unit(2);
        margin-bottom: unit(2);
    }

    margin-top: rem(40);
    margin-bottom: unit(4);
}

// No Sidebar on Full Width
//
.page-template-full-width .content-area,
.post-template-full-width-title-excerpt-image .content-area,
.post-template-full-width-title-excerpt-overlay-image .content-area,
.page-template-full-width-title-excerpt-image .content-area,
.page-template-full-width-title-excerpt-overlay-image .content-area,
.archive:not(.search-results):not(.search-no-results) .content-area {
    @include span-columns(12);

    padding-right: 0;
}

// Remove margin on single posts
//
.single .content-area,
.page .content-area {
    // margin-bottom: 0;
}

// Toggle section on Taxonomy Region page (region main)
//
@include media( $tablet ) {
    .content-container {
        overflow: hidden;
    }
    body.tax-region .content-area,
    body.home .content-area {
        @include transition(left 0.5s ease-in-out);
        // display: none;
        position: absolute;
        top: 0;
        left: -100%;

        padding-right: $gutter / 2;
        padding-left: $gutter / 2;
        margin-top: $gutter / 2;
        margin-bottom: 0;

        &.active {
            // display: block;
            left: 0%;
        }
    }
}

// Load More Posts
//
.load-more {
    padding: rem(30) 0 rem(60);
    text-align: center;
}
