// ===========================================================================
//
// Second Featured Articles
//
// ===========================================================================

article.third-featured {
    @include span-columns(6);

    position: relative;

    overflow: hidden;

    width: calc(50% - #{unit(1)});
    margin-right: unit(2);
    margin-bottom: unit(2);

    padding-left: unit(2);

    &:nth-child(even) {
        margin-right: 0;
    }

    .featured-entry-header {
        padding: 0;
    }

    .featured-entry-content {
        color: $hex-light-grey;
        font-size: rem(14);
        line-height: rem(21);

        a {
            color: $hex-light-grey;
        }
    }

    .more-link {
        text-transform: uppercase;
    }
}

@include media( $mobile ) {
    article.third-featured {
        @include span-columns(2);
        padding-left: 0;
    }
}
