// ===========================================================================
//
// Full Width - Excerpt overlay Image
//
// ===========================================================================

.post-template-full-width-title-excerpt-overlay-image #primary article.post,
.page-template-full-width-title-excerpt-overlay-image #primary article.page {

    .entry-header {
        position: relative;
        margin-bottom: 0;

        .entry-header-content-container {
            @include position( absolute, 15% null null 7% );
            z-index: 1;
            max-width: rem(750);
        }
    }

    .entry-title {
        font-size: rem(48);
        line-height: rem(60);
        color: $hex-real-white;
    }

    .entry-excerpt {
        font-size: rem(24);
        line-height: rem(36);
        color: $hex-real-white;
        padding: rem(8) 0 rem(25);
    }

    .entry-meta {
        .byline {
            font-size: rem(12);
            line-height: rem(36);
            margin: 0;
        }

        .posted-on {
            margin-left: rem(15);
        }
    }

    .entry-featured-image {
        @include media($desktop) {
            // Set a min-height in case the image is missing.
            min-height: 480px;
        }
        position: relative;

        &::after {
            content: '';
            display: block;
            @include position( absolute, 0 0 0 0 );
            opacity: 0.6;
            background-image: linear-gradient(-90deg, rgba(0,0,0,0.00) 0%, #08121F 48%);

            @include media( $tablet ) {
                display: none;
            }
        }
    }

    .social-sharing {
        float: none;
        display: flex;
        justify-content: center;

        margin: rem(50) 0;

        li:last-child {
            margin-right: 0;
        }
    }

}

.post-template-full-width-title-excerpt-overlay-image .related-articles,
.page-template-full-width-title-excerpt-overlay-image .related-articles {

    .related-articles-title {
        font-size: rem(18);
        line-height: rem(30);
        color: $tundora;
        text-transform: uppercase;
        padding-top: rem(30);
        margin-bottom: rem(20);
    }

    article.second-featured {
        @include span-columns(4);
        @include tablet-single;

        margin-bottom: rem(40) !important;
    }
}

@include media($tablet) {
    .post-template-full-width-title-excerpt-overlay-image #primary article.post,
    .page-template-full-width-title-excerpt-overlay-image #primary article.page {

        .entry-header {
            margin-bottom: 0;
            padding: 0;

            .entry-header-content-container {
                position: static;
            }
        }

        .entry-title {
            color: $shark;
        }

        .entry-excerpt {
            color: $shark;
        }

        .entry-featured-image {
            margin-bottom: rem(18);
        }

        .entry-meta {
            margin: 0 0 rem(18);

            .byline {
                font-size: rem(12);
                line-height: rem(14);
                margin: 0;
            }

            .posted-on {
                margin: 0;
            }
        }

        .social-sharing {
            margin: 0;
            padding: 0;
        }
    }
}
