// ===========================================================================
//
// Search Listing Articles
//
// ===========================================================================

$search-article-min-height: 200px;
$search-article-aside-min-width: 40%;

article.search-article {
    @include media($mobile) {
        flex-direction: column;
    }

    display: flex;

    min-height: $search-article-min-height;
    margin-bottom: unit(2);
    padding-bottom: 0 !important;

    background-color: $hex-real-white;

    .featured-entry-meta {
        margin-bottom: 0;
        padding: rem(25);
    }

    .featured-entry-header {
        padding-bottom: 0;

        background-color: $hex-real-white;
    }

    .entry-footer {
        font-size: rem(12);
        line-height: 1.5;

        padding: 0 rem(25) rem(20);

        text-transform: uppercase;
        text-overflow: ellipsis;

        color: $oslo-gray;

        a {
            color: $picton-blue;
        }

        .byline,
        .entry-categories {
            display: inline;
        }
    }

    .entry-categories a {
        color: $silver-sand;
    }
}

.search-article__image {
    @include media($mobile) {
        min-height: $search-article-min-height;
    }

    display: flex;

    min-width: $search-article-aside-min-width;

    background-position: 50%;
    background-size: cover;
}

.search-article__content {
    display: flex;
    flex-direction: column;

    justify-content: space-between;
}
