// ===========================================================================
//
// Second Featured Articles
//
// ===========================================================================

$second-featured-height: 400px;
$second-featured-height-mobile: 400px;

article.second-featured {
    @include span-columns(6);
    @include media($mobile) {
        width: 100%;
        height: $second-featured-height-mobile;
        margin-right: 0;
    }

    display: flex;
    overflow: hidden;
    flex-direction: column;
    width: calc(50% - #{unit(1)});

    height: $second-featured-height;
    margin-right: unit(2);
    margin-bottom: unit(2);

    background-color: $hex-real-white;

    &:nth-child(even) {
        margin-right: 0;
    }

    .featured-entry-meta {
        margin-top: rem(25);
        margin-left: rem(25);
    }

    .featured-entry-header {
        width: 100%;
    }

    .featured-entry-categories {
        @include media($tablet) {
            margin-bottom: rem(10);
        }

        font-size: rem(13);
        line-height: 1.5;

        overflow: hidden;

        margin-bottom: rem(-5);

        white-space: nowrap;
        text-transform: uppercase;
        text-overflow: ellipsis;

        a {
            padding: 0 3px;

            &:first-child {
                padding-left: 0;
            }

            &:last-child {
                padding-right: 0;
            }
        }
    }
}

.second-featured__image {
    @include media($mobile) {
        min-height: ($second-featured-height-mobile / 2);
    }

    display: flex;

    min-height: ($second-featured-height / 2);

    background-color: $hex-light-grey;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: cover;
}

.second-featured__content {
}

// handle this component differently in the sidebar
#secondary article.second-featured {
    width: 100%;
    margin-right: 0;

    &:last-child {
        margin-bottom: 0;
    }
}

@include media($mobile) {
    #main article.second-featured {
        @include span-columns(2 of 2);
        margin-right: 0 !important;
        margin-bottom: $gutter / 2;
    }
}
