// ===========================================================================
//
// Comments
//
// ===========================================================================

.comments-area {
    padding: rem(40) rem(40);
    background-color: $hex-real-white;

    @include media( $tablet ) {
        padding-left: rem(20);
        padding-right: rem(20);
    }

    > .comments-list {
        margin-bottom: rem(55);

        @include media( $tablet ) {
          margin-bottom: 0;
        }
    }
}

.comment-reply-title,
.comments-title {
    font-family: "SophosSansMedium", $font-fallback;
    font-size: rem(24);
    line-height: rem(30);
    margin-bottom: rem(13);

    color: $tangaroa;

    span {
        color: $sophos-blue;
    }

    #cancel-comment-reply-link {
        float: right;
    }
}

.comment {
    .comment-wrapper {
        padding: rem(30) rem(18);

        @include media( $tablet ) {
            padding: rem(15) 0;
        }
    }

    .gravatar {
        float: left;
        overflow: hidden;
        border-radius: 100%;

        @include media( $tablet ) {
            margin-bottom: rem(15);
        }

        img {
            overflow: hidden;
            border-radius: 100%;

            @include media( $tablet ) {
                max-width: rem(40);
            }
        }
    }

    .comment-meta,
    .comment-content {
        margin-left: rem(80);
    }

    @include media( $tablet ) {
        .comment-meta {
            float: left;
            margin-left: rem(10);
        }
        .comment-content {
            clear: both;
            margin-left: 0;
        }
    }

    .comment-meta {
        font-family: "SophosSansMedium", $font-fallback;
        font-size: rem(14);
        line-height: rem(24);

        @include media( $tablet ) {
            line-height: rem(20);
        }

        .comment-author,
        .comment-meta-item {
            display: inline-block;

            @include media( $tablet ) {
                display: block;
            }
        }

        .comment-author {
            color: $sophos-blue;
            a {
                color: $sophos-blue;
            }
        }

        .comment-meta-item {
            color: $aluminum;
            margin-left: rem(14);

            @include media( $tablet ) {
                margin-left: 0;
            }

            a {
                color: $aluminum;
            }
        }
    }

    .comment-content {
        font-size: rem(14);
        line-height: rem(24);

        .comment-reply-link {
            font-size: rem(14);
            line-height: rem(24);
            color: $picton-blue;
        }
    }

    &.bystaff {
        > .comment-wrapper {
            background-color: $hex-faint;
            border-left: 4px solid $cornflower;
            padding-left: rem(14);

            .comment-author {
                position: relative;

                &::after {
                    content: '';
                    display: inline-block;
                    @include svg-icon('icon-sophos-staff', 15px, 15px, $sophos-blue);
                    @include position( relative, 3px null null 3px);
                }

                &::before {
                    content: 'Sophos Staff';
                    display: none;
                    @include svg-icon('sophos-staff-anchor', 81px, 29px);
                    @include position( absolute, -29px -30% null null);

                    font-family: "SophosSansMedium", $font-fallback;
                    font-size: rem(10);
                    color: $sophos-blue;
                    text-align: center;
                }

                &:hover::before {
                    display: block;
                }
            }
        }
    }
}

@include media( $desktop ) {
    .child-comments {
        margin-left: rem(42);
    }
    .depth-1 > .child-comments {
        margin-left: rem(98);
    }
}

.form-allowed-tags {
    @include type-style-small;
    // You might just want to hide this
    // display: none;

    margin-bottom: ms(0);
    code {
    }
}

.comment-notes {
    display: none;
}

.comment-respond {
    padding: 0 rem(120);

    @include media( $tablet ) {
        padding: 0;
    }

    .comment-reply-title small,
    .logged-in-as {
        font-size: rem(14);
    }

    .comment-form {
        label {
            margin: 0 0 rem(4);
            font-family: 'SophosSansMedium', $font-fallback;
            font-size: rem(12);
            line-height: rem(16);
            color: $oslo-gray;
        }
        input[type=text],
        input[type=email],
        input[type=url],
        textarea {
            margin: 0;
            font-size: rem(14);
            border: 1px solid $silver-sand;
            border-radius: 3px;
        }
    }

    .comment-form-comment textarea {
        height: rem(70);
    }

    .comment-form-author,
    .comment-form-email,
    .comment-form-url {
        max-width: rem(250);

        @include media( $tablet ) {
            max-width: none;
        }
    }

    .form-submit {
        margin-bottom: 0;
    }
    .submit {
        @extend .btn;
        @extend .btn-green;

        @include media($mobile) {
            width: 100%;
        }

        margin-bottom: 0;
    }
}

.comments-list .comment-respond {
    padding: rem(6) 0 rem(30) rem(98);

    @include media( $tablet ) {
        padding-left: 0;
    }
}

// Pingbacks
//
.pingback {
    .comment-body {
        margin-bottom: unit(1);
        padding-bottom: unit(1);
        padding-left: 0;
    }
    + .comment .comment-body {
        @include media($mobile) {
            margin-top: unit(1);
        }

        margin-top: unit(2);
    }
}
