// ===========================================================================
//
// Featured Entry
//
// ===========================================================================

.article-wrapper {
    @include clearfix;
}

.featured-entry-header {
    padding: rem(25);

    align-self: flex-start;
}

.featured-entry-title,
article.post .featured-entry-title {
    font-size: rem(24);
    line-height: ms(1);

    @include media($tablet) {
      margin-top: 0;
    }
}

.featured-entry-title a {
    @include type-dark-links;
}

.featured-entry-categories {
    color: $hex-light-grey;
    ul {
        font-size: rem(13);
        line-height: ms(0);

        margin: 0;
        padding: 7px 0 0 15px;

        list-style: none;

        text-transform: uppercase;
    }
    li {
        margin-bottom: rem(5);
    }
    a {
        @include type-light-links;
    }

    .post-categories {
        li {
            overflow: hidden;

            max-width: 160px;

            white-space: nowrap;
            text-overflow: ellipsis;

            // Show 4 items
            &:nth-child(n+5) {
                display: none;
            }
        }
    }
}

.featured-entry-meta {
    display: flex;

    margin-bottom: unit(2);

    flex-wrap: nowrap;
}

.featured-entry-date {
    display: flex;
    flex-direction: column;

    width: 80px;
    height: 80px;

    color: $hex-real-white;
    background: $hex-secondary;

    align-items: center;
    justify-content: center;

    .month {
        @include sophos-sans-semibold;

        font-size: rem(15);
        line-height: ms(0);

        text-transform: uppercase;
    }

    .day-num {
        @include sophos-sans-light;

        font-size: rem(26);
        line-height: ms(0);
    }
}
