// ===========================================================================
//
// Primary Header
//
// ===========================================================================

.primary-header-navigation {
    @include position(absolute, 0 $gutter null null);
    @include sophos-sans-light;
    @include media( $tablet ) {
        right: $gutter / 2;
    }

    overflow: hidden;

    height: $header-bar-height;
    padding-left: 180px; // width of .site-logo

    a {
        @include transition-default;

        font-size: rem(15);
        line-height: $header-bar-height + $header-content-offset + 2;

        display: block;

        height: $header-bar-height;
        padding: 0 rem(15);

        text-decoration: none;
        text-transform: uppercase;

        opacity: .65;
        color: $hex-real-white;


        &:hover {
            opacity: 1;
        }
    }

    > ul {
        @include clearfix;
        @include media( $tablet ) {
            display: none;
        }

        margin: 0;
        padding: 0;

        list-style: none;

        li {
            display: block;
            float: left;

            height: $header-bar-height;

            &:last-child a {
                padding-right: 0;
            }

            &.has-icon a {
                display: flex;

                align-items: center;
            }
        }
    }

    .navigation-title {
        @include media( $tablet ) {
            display: block;
        }
        @include media( $mobile ) {
            display: none;
        }

        display: none;
        float: right;

        a {
            border-bottom-color: transparent;
        }

        .toggle {
            @include hide-text;

            display: none; // Temporary

            opacity: 1;

            &:after {
                @include position(absolute, 26px 0px null null);
                @include svg-icon('icon-arrow-toggle', 12px, 8px, $hex-real-white);

                display: block;

                content: '';

                opacity: .6;
                background-size: contain;
            }
        }
    }

    .is-icon {
        display: flex !important;

        padding-right: rem(10);
        padding-left: rem(10);

        align-items: center;

        .drop-target:after {
            display: none;
        }
    }

    .is-icon a {
        @include hide-text;

        display: inline-block;

        height: $header-bar-height !important;
        margin: 0;
        padding: 0;

        vertical-align: middle;

        background-position: center !important;
        background-size: contain;
    }

    .icon-download a {
        @include svg-icon('icon-downloads', 21px, 19px, $hex-real-white);
    }

    .icon-search a {
        @include svg-icon('icon-search', 19px, 19px, $hex-real-white);
    }

    .icon-account a {
        @include svg-icon('icon-account', 22px, 19px, $hex-real-white);
    }
}
