// ===========================================================================
//
// Colors
//
// ===========================================================================


// Colors
// ---------------------------------------------------------------------------

// White
$hex-white: #f9f9f9;
$hex-real-white: #fff;
$wild-sand: #F6F6F6; // mobile nav white

// Colors
$hex-red: #b2432c;
$hex-highlight: #fff9c0;
$sophos-blue: #005bb5;
$tangaroa: #00193b; // Dark blue text
$picton-blue: #33A6E4; // Light blue
$forest-green: #3FAE29;
$cornflower: #9ECEE9; // Another light blue
$duck-egg-blue: #cce4f4; // Menu borders
$shark: #242629; // post template title black

// Greys/
$hex-faint: #f8f8f8;
$hex-grey: tint(#000, 50%);
$aluminum: #9b9b9b; // Light grey text
$bombay: #afafaf; // Border
$iron: #E2E5E7;
$tundora: #4A4A4A; // Dark grey
$dove-gray: #6A6A6A;
$oslo-gray: #83888E;
$silver-sand: #C2C6CA;
$alto: #D4D4D4;
$steel: #7f8b93;


// Mappings
// ---------------------------------------------------------------------------

// Default (Text)
$hex-default: tint(#000, 35%);
$hex-default-hover: shade($hex-default, 20%);

// Light Text
$hex-light-grey: $aluminum;
$hex-light-grey-hover: shade($hex-light-grey, 20%);

// Background
$hex-background: $hex-faint;

// Primary
$hex-primary: $sophos-blue;
$hex-primary-hover: shade($hex-primary, 20%);

// Secondary
$hex-secondary: $tangaroa;
$hex-secondary-hover: shade($hex-secondary, 20%);

// Borders
$hex-border: lighten($bombay, 10%);
$hex-border-hover: shade($hex-border, 20%);

// Footer
$hex-primary-footer-bg: tint($sophos-blue, 8%);
$hex-secondary-footer-bg: $sophos-blue;
$hex-footer-border: tint($hex-primary-footer-bg, 20%);
$hex-footer-title: tint($sophos-blue, 85%);
$hex-footer-text: tint($sophos-blue, 65%);
