// ===========================================================================
//
// Icons
//
// ===========================================================================

.icon {
    @include hide-text;

    margin: 0;
    padding: 0;

    background-size: contain;
}

.icon-search-secondary {
    @include svg-icon('icon-search-secondary', 15px, 15px, $hex-real-white);
}
.icon-twitter {
    @include svg-icon('icon-twitter', 14px, 12px, $hex-real-white);
}
.icon-facebook {
    @include svg-icon('icon-facebook', 7px, 14px, $hex-real-white);
}
.icon-googleplus {
    @include svg-icon('icon-googleplus', 13px, 14px, $hex-real-white);
}
.icon-linkedin {
    @include svg-icon('icon-linkedin', 14px, 13px, $hex-real-white);
}
.icon-rss {
    @include svg-icon('icon-rss', 14px, 14px, $hex-real-white);
}
