.s-ad-xg-firewall__sophos-logo-header-svg {
    vertical-align: top;
}
.s-ad-xg-firewall__title {
    font-size: 22px;
    line-height: 1.125;
    text-align: center;
    color: #fff;
}
.s-button--orange {
    color: #000;
    border-color: #f38332;
    background-color: #f38332;
}
.s-button--orange:hover {
    color: #fff;
    border-color: #e5680d;
    background-color: #e5680d;
}
@media (min-width: 769px) {
    .s-ad-xg-firewall__content {
	margin-right: 45px;
	margin-left: 38%;
    }
    .s-ad-xg-firewall__sophos-logo-header {
	display: none;
    }
}
@media (max-width: 768px) {
    .s-ad-xg-firewall {
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-ms-flex-direction: column;
	flex-direction: column;
	background-position: top;
	background-size: 361px 172px;
    }
    .s-ad-xg-firewall__sophos-logo {
	display: none;
    }
    .s-ad-xg-firewall__sophos-logo-header-svg,
    .s-ad-xg-firewall__sophos-logo-svg {
	width: 64px;
	height: 11px;
	background-size: 64px 11px;
    }
    .s-ad-xg-firewall__title {
	font-size: 14px;
	margin-bottom: 20px;
	text-transform: uppercase;
    }
    .s-ad-xg-firewall__dead {
	display: block;
    }
}
